.skills {
    background-color: #000000;
}

.skills__title {
    border-color: #ffffff;
    color: #ffffff;
}

.skills__list {
    padding: 0;
}

.skills__item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;

    margin: 10px 0;
}

.skills__name {
    font-size: 20px;
    color: #ffffff;
    text-align: right;

    -ms-flex-preferred-size: 165px;

        flex-basis: 165px;
}

.progress {
    display: -ms-flexbox;
    display: flex;
    margin-left: 90px;
}

.progress__point {
    border: 1px solid #f6b93b;
    border-radius: 15px;

    margin: 0 10px;
    
    height: 30px;
    width: 30px;
}

.progress__point--filled {
    background-color: #f6b93b;
}


